import React from 'react'
import styled from 'styled-components'
import { connect } from 'react-redux'

import { toggleUserModal } from '../../../store/users/action'
import Header from './Header'
import TableContainer from './TableContainer'
import UserModal from './UserModal'

const Wrapper = styled.section`
  font-size: 1rem;

  .users-header {
    margin: 62px 0 0;
  }

  .table-container {
    margin: 38px 0 0;
  }

  @media (max-width: 1300px) {
    font-size: 0.8rem;
  }
`

class Users extends React.Component {
  state = {
    searchKey: ''
  }

  handleFeatureClick = (user, featureId) => () => {
    if ([2, 4, 8].includes(featureId))
      this.props.toggleUserModal({ id: user.id, force: true, featureId, view: 'featureToggle' })
  }

  render() {
    const { isModalOpen } = this.props

    return (
      <Wrapper>
        {isModalOpen && <UserModal />}
        <Header
          className="users-header"
          onAddNewUser={() => this.props.toggleUserModal({ force: true, view: 'add' })}
        />
        <TableContainer
          className="table-container"
          onAddNewUser={() => this.props.toggleUserModal({ force: true, view: 'add' })}
          onFeaturesClick={id => this.props.toggleUserModal({ force: true, id, view: 'features' })}
          onFeatureClick={this.handleFeatureClick}
        />
      </Wrapper>
    )
  }
}

export default connect(
  ({ users }) => ({ isModalOpen: users.isModalOpen }),
  { toggleUserModal }
)(Users)
