import React from 'react'
import { connect } from 'react-redux'
import { always, cond, equals, T } from 'ramda'
import {
  Dialog,
  DialogContent,
  DialogTitle,
  Fab,
  DialogActions,
  withStyles
} from '@material-ui/core'
import SwipeableViews from 'react-swipeable-views'
import classNames from 'classnames'

import { addUser, toggleFeature, toggleUserModal } from '../../../../store/users/action'
import AddView from './AddView'
import FeaturesView from './FeaturesView'
import CallForwardFeature from './CallForwardFeature'
import VoiceMailFeature from './VoiceMailFeature'
import MusicFeature from './MusicFeature'

const styles = {
  fab: {
    width: 108,
    height: 33,
    fontSize: '18px',
    fontFamily: 'Source Sans Pro',
    textTransform: 'inherit',
    borderRadius: '29px'
  },
  next: {
    background: '#09d357',
    color: '#fff',
    '&:hover': {
      background: '#09d357',
      color: '#fff'
    }
  },
  centered: {
    width: 171,
    background: '#09d357',
    color: '#fff',
    '&:hover': {
      background: '#09d357',
      color: '#fff'
    }
  },
  actions: {
    padding: '0 30px 30px'
  },
  swipeable: {
    height: '676px'
  }
}

class UserModal extends React.Component {
  state = {
    swipeIndex:
      this.props.view === 'add'
        ? 0
        : this.props.view === 'features'
        ? 1
        : this.props.view === 'featureToggle'
        ? 2
        : 0,
    featureId: this.props.featureId || 2
  }

  getDialogTitle = cond([
    [equals(0), always('New User')],
    [equals(1), always('User Features')],
    [T, always(' ')]
  ])

  handleSaveChanges = () => {
    this.setState(prev => ({
      swipeIndex: prev.swipeIndex > 0 ? prev.swipeIndex - 1 : prev.swipeIndex
    }))
  }

  handleInputChange = event => {
    const { name, value } = event.target
    this.setState({ [name]: value })
  }

  handleApplyChanges = () => this.props.toggleUserModal({ force: false })

  handleMoveNext = () => {
    this.setState(prev => ({ swipeIndex: prev.swipeIndex + 1 }))
  }

  handleActionClick = swipeIndex => () => {
    if (swipeIndex === 0) this.handleAddUser()
    if (swipeIndex === 1) this.handleApplyChanges()
    if (swipeIndex === 2) this.handleSaveChanges()
  }

  handleAddUser = () => {
    this.props.addUser({
      firstName: this.state.firstName || '',
      lastName: this.state.lastName || '',
      email: this.state.email || '',
      extensionNumber: this.state.extensionNumber || '',
      directPhone: this.state.directPhone || ''
    })
    this.handleMoveNext()
  }

  handleFeatureClick = featureId => () => {
    if ([2, 4, 8].includes(featureId)) this.setState({ featureId }, () => this.handleMoveNext())
  }

  onFeatureToggle = e => {
    this.props.toggleFeature({
      checked: e.target.checked,
      userId: this.props.target.id,
      featureId: this.state.featureId
    })
  }

  render() {
    const { isModalOpen, target, classes, users } = this.props
    const { swipeIndex, featureId } = this.state
    const { id } = target || {}
    const { features } = (users || []).find(user => user.id === id) || { features: {} }

    return (
      <div>
        <Dialog open={isModalOpen} maxWidth="md">
          <DialogTitle
            style={{ padding: '24px 62px 0', fontSize: '32px', fontFamily: 'Open Sans' }}
          >
            {this.getDialogTitle(swipeIndex)}
          </DialogTitle>
          <DialogContent>
            <SwipeableViews index={swipeIndex} animateHeight disabled>
              <AddView onChange={this.handleInputChange} />
              <FeaturesView features={features} onFeatureClick={this.handleFeatureClick} />
              <div>
                {featureId === 2 ? (
                  <CallForwardFeature visible={swipeIndex === 2} onToggle={this.onFeatureToggle} />
                ) : featureId === 4 ? (
                  <VoiceMailFeature onToggle={this.onFeatureToggle} />
                ) : (
                  <MusicFeature onToggle={this.onFeatureToggle} />
                )}
              </div>
            </SwipeableViews>
          </DialogContent>
          <DialogActions
            className={classes.actions}
            style={{ justifyContent: swipeIndex === 0 ? 'flex-end' : 'center' }}
          >
            <Fab
              variant="extended"
              className={classNames(classes.fab, classes.centered)}
              style={{ display: swipeIndex === 0 ? 'none' : 'block' }}
              onClick={this.handleActionClick(swipeIndex)}
            >
              {swipeIndex === 1 ? 'Apply Changes' : 'Save Changes'}
            </Fab>
            <Fab
              variant="extended"
              className={classNames(classes.fab, classes.next)}
              style={{ display: swipeIndex === 0 ? 'block' : 'none' }}
              onClick={this.handleActionClick(swipeIndex)}
            >
              Next
            </Fab>
          </DialogActions>
        </Dialog>
      </div>
    )
  }
}

const mstp = ({ users }) => ({
  users: users.users,
  isModalOpen: users.isModalOpen,
  target: users.target,
  view: users.view,
  featureId: users.featureId
})

export default connect(
  mstp,
  { addUser, toggleFeature, toggleUserModal }
)(withStyles(styles)(UserModal))
