import React from 'react'
import styled from 'styled-components'
import { cond, always, equals } from 'ramda'
import {
  Input,
  Checkbox,
  FormControl,
  Select,
  LinearProgress,
  InputLabel,
  MenuItem,
  Button
} from '@material-ui/core'

import PlusSvg from '../../svgs/Plus'

const Wrapper = styled.div`
  padding: 40px 40px 0;

  .aligner {
    min-width: 700px;
    max-width: 700px;
    width: 100%;
    margin: 0 auto;

    > div {
      flex: 1;
      max-width: 45%;

      &:nth-child(2) {
        padding: 1px 0 0;
      }
    }
  }

  .checkbox-container > label > span:nth-child(2) {
    padding-top: 2px;
    color: #777676;
    font-family: 'Open Sans';
    font-size: 0.85rem;
    font-weight: 400;
    cursor: pointer !important;
  }

  .checkbox {
    padding-top: 0;
    padding-left: 0;
  }

  .basic-input {
    font-family: 'Open Sans';
    font-size: 1.25rem;
    font-weight: 400;

    &::after {
      border-bottom: 2px solid #09d357;
    }
  }

  .select {
    > label {
      color: #7d7d7d;
    }

    > div::after {
      border-bottom: 2px solid #09d357;
    }
  }

  .progress-container {
    max-width: 300px;
    width: 100%;

    > div {
      flex: 1;
      height: 7px;
      border-radius: 9px;

      > div {
        height: 7px;
        background-color: #09d357;
      }
    }

    > span {
      display: block;
      min-width: 40px;
      margin: 0 0 0 20px;
      font-family: 'Open Sans';
      font-size: 1.1875rem;
      font-weight: 400;
    }
  }

  .new-device-btn {
    svg {
      max-width: 20px;
      max-height: 20px;
      fill: #09d357;
      margin: 0 10px 0 0;
    }

    .text {
      color: #777676;
      font-family: 'Open Sans';
      font-size: 1rem;
      font-weight: 400;
      text-transform: initial;
    }
  }
`

const AddView = ({ onChange }) => {
  const [password, setPassword] = React.useState('')
  const [userLevel, setUserLevel] = React.useState('')
  const [timeZone, setTimeZone] = React.useState('')
  const [ringingTime, setRingingTime] = React.useState('')
  const [language, setLanguage] = React.useState('')

  const passwordLevel =
    password.length < 4 ? 0 : password.length >= 4 && password.length < 8 ? 50 : 100

  return (
    <Wrapper>
      <div className="aligner flex justify-between">
        <div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              name="firstName"
              onChange={onChange}
              placeholder="First Name"
              inputProps={{ tabIndex: '1' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="email"
              name="email"
              onChange={onChange}
              placeholder="Email for login"
              inputProps={{ tabIndex: '2' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="password"
              name="passwordConfirm"
              placeholder="Re-type Password"
              inputProps={{ tabIndex: '3' }}
            />
          </div>
          <div className="input-container checkbox-container">
            <label className="flex align-start">
              <Checkbox className="checkbox" />
              <span>Send email to alternate address</span>
            </label>
          </div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="tel"
              name="extensionNumber"
              onChange={onChange}
              placeholder="Extension number"
              inputProps={{ tabIndex: '5' }}
            />
          </div>
          <div className="input-container checkbox-container">
            <label className="flex align-start">
              <Checkbox className="checkbox" />
              <span>Include user in the company directory</span>
            </label>
          </div>
          <div className="input-container checkbox-container" style={{ marginBottom: '25px' }}>
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-user-level">User Level</InputLabel>
              <Select
                value={userLevel}
                onChange={e => setUserLevel(e.target.value)}
                inputProps={{
                  name: 'userLevel',
                  id: 'add-user-level',
                  tabIndex: '6'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>Level 1</MenuItem>
                <MenuItem value={2}>Level 2</MenuItem>
                <MenuItem value={3}>Level 3</MenuItem>
              </Select>
            </FormControl>
          </div>
          <div className="input-container checkbox-container" style={{ marginBottom: '25px' }}>
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-language">Language</InputLabel>
              <Select
                value={language}
                onChange={e => setLanguage(e.target.value)}
                inputProps={{
                  name: 'language',
                  id: 'add-user-language',
                  tabIndex: '7'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>English</MenuItem>
                <MenuItem value={2}>Romanian</MenuItem>
                <MenuItem value={3}>Spain</MenuItem>
              </Select>
            </FormControl>
          </div>
        </div>
        <div>
          <div className="input-container" style={{ marginBottom: '25px' }}>
            <Input
              fullWidth
              className="basic-input"
              name="lastName"
              onChange={onChange}
              placeholder="Last Name"
              inputProps={{ tabIndex: '1' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '15px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="password"
              name="password"
              onChange={e => setPassword(e.target.value)}
              placeholder="Password"
              inputProps={{ tabIndex: '2' }}
            />
          </div>
          <div className="progress-container flex justify-between align-center">
            <LinearProgress value={passwordLevel} variant="determinate" />
            <span>
              {cond([
                [equals(0), always('Bad')],
                [equals(50), always('Good')],
                [equals(100), always('Best')]
              ])(passwordLevel)}
            </span>
          </div>
          <div className="input-container" style={{ marginBottom: '15px', marginTop: '31px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="email"
              name="notification-email"
              placeholder="Notification Email"
              inputProps={{ tabIndex: '4' }}
            />
          </div>
          <div className="input-container" style={{ marginBottom: '16px' }}>
            <Input
              fullWidth
              className="basic-input"
              type="tel"
              name="directPhone"
              onChange={onChange}
              placeholder="Direct phone"
              inputProps={{ tabIndex: '5' }}
            />
          </div>
          <div className="new-device-btn-container">
            <Button className="new-device-btn">
              <PlusSvg />
              <span className="text">New Device</span>
            </Button>
          </div>
          <div
            className="input-container checkbox-container"
            style={{ marginTop: '3px', marginBottom: '25px' }}
          >
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-time-zone">Time Zone</InputLabel>
              <Select
                value={timeZone}
                onChange={e => setTimeZone(e.target.value)}
                inputProps={{
                  name: 'timeZone',
                  id: 'add-time-zone',
                  tabIndex: '6'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>GMT</MenuItem>
                <MenuItem value={2}>GMT+1</MenuItem>
                <MenuItem value={3}>GMT+2</MenuItem>
              </Select>
            </FormControl>
          </div>
          <div className="input-container checkbox-container" style={{ marginBottom: '25px' }}>
            <FormControl fullWidth classes={{ root: 'select' }}>
              <InputLabel htmlFor="add-ringing-time">Ringing Time</InputLabel>
              <Select
                value={ringingTime}
                onChange={e => setRingingTime(e.target.value)}
                inputProps={{
                  name: 'ringingTime',
                  id: 'add-ringing-time',
                  tabIndex: '7'
                }}
              >
                <MenuItem value={''} />
                <MenuItem value={1}>1 hr</MenuItem>
                <MenuItem value={2}>2 hrs</MenuItem>
                <MenuItem value={3}>3 hrs</MenuItem>
              </Select>
            </FormControl>
          </div>
        </div>
      </div>
    </Wrapper>
  )
}

export default AddView
